body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Oxygen',
    
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1A1721;
}
nav{
  background-color: #1A1721 !important;
  border-bottom: solid 2px rgb(226, 225, 235);
  border-radius: 0px 0px 12px 12px;
}
.page-footer{
  background-color: #1A1721 !important;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.content{
  min-height: calc(100vh - 70px - 64px);
}

.cart{
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  cursor: pointer;
  border-radius: 5%;
  z-index: 5;
  padding: 1rem;
}

@media (min-width: 767px){
  .cart{
    bottom: unset;
    top:5rem;
  }
}

.material-icons{
  font-size: 35px;
  color: white;

}

.basket-list{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  background-color: #fff;
  width: 50rem;
  max-width: 95%;
  box-shadow: 1px 3px 8px 5px #8c8bc0;
  animation: show 500ms ease-in-out;
}

@keyframes show{
  from{
    top: 70%;
    opacity: 0;
  }
  to{
    top: 50%;
    opacity: 1;
  }
}

.basket-clear{
  cursor: pointer;
  position: absolute;
  top:0.5rem;
  right: 0.5rem;
  color: #fff;
}

.basket-quantity{
  cursor:pointer;
  vertical-align: middle;
  color:#341974;
  font-size: 20px;
}

#toast-container {
  top: 3rem !important;
  animation: fade-in 250ms ease-in-out;
}

@keyframes fade-in{
  from{
    transform: translateY(2rem);
    opacity: 0;
  }
  to{
    transform: translateY(0rem);
    opacity: 1;
  }
}

.right{
  font-weight: 600 !important;
  font-size: 16px !important;
}
.btn{
  background-color: #341974;
  font-weight: 600 !important;
}

.list{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1rem;
  
}
.card-title{
  color: #341974;
  font-size: 20px !important;
  font-weight: 600 !important;
}

.card{


  background-color: #fbfbff;
  outline: solid 1px #e0f2f1;
  border-radius: 5px;
  padding: 5px;
  min-height: 440px;
  display: flex;
  flex-direction: column;
  box-shadow: 2px 4px 10px -1px rgba(213, 197, 255, 0.4);
  
}
.card-content{
  flex-grow: 1;
  
}
img, iframe{
  max-width: 100%;
  
}
iframe{
  width: 600px;
  height: 350px;
}

.collection .collection-item.active {
  background-color: #1A1721;
}